@import 'assets/styles/variables';

.menu {
    padding: 20px;
}

.menuItem {
    list-style-type: none;
    color: $blue;
    margin: 10px 0;

    &:first-child {
        font-size: 20px;
        color: #ccc;
        border-bottom: 1px solid #ccc;
